body {
  font-family: sans-serif;
  background-color: maroon;
  display: flex;
  flex-direction: column;
  margin: 0px;
}

#deadLine {
  background-color: maroon;
  color: #ffdab9;
  border: none;
  outline: none;
  padding-bottom: 20px;
  font-size: 36px;
}

#dateHeader {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* background-color: #397963; */
  color: #ffdab9;
  box-sizing: border-box;
  font-size: 32px;
  width: 100%;
}

#todayDate {
  text-align: center;
  font-size: 55px;
  box-sizing: border-box;
  margin-left: 20px;
}

#app {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 500px;
  padding-top: 20px;
  padding-bottom: 10px;
  /* background-color: white; */
  border-radius: 4px;
}

#addTodo {
  background-color: #ffdab9;
  color: maroon;
  height: 55px;
  width: 140px;
  font-size: 20px;
  margin-left: 5px;
  box-sizing: border-box;
  border: 1px solid #c0a286;
  border-radius: 4px;
}

#todoInput {
  height: 55px;
  width: 370px;
  font-size: 20px;
  box-sizing: border-box;
  border: none;
  border-radius: 4px;
}

#todoListUl {
  width: 100%;
  padding: 0px;
}

form {
  display: flex;
  flex-direction: row;
}
